body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__imgBlock {
  justify-content: center;
  margin-bottom: 40px;
  display: flex;
}

.card__imgBlock > img {
  width: 160px;
  height: 134px;
}

.card__labelBlock {
  text-align: left;
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__productCode {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__rate > div {
  text-align: right;
  align-items: center;
  height: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__button {
  text-align: center;
  color: #fff;
  box-sizing: border-box;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .4s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.priceBlock {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.priceBlock__label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.priceBlock__price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars-4 {
  display: flex;
}

.stars-4 > div {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars-4 > div:not(:first-child) {
  margin-left: 4px;
}

.stars-4 > div:nth-last-child(n+2) {
  background-image: url("star-active.edda4c43.svg");
}
